import React from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import './Header.css';

function Header() {
    return (
        <div className="header shadow">
            <div className="header__left">
                <MenuIcon />
                <img className="header__logo" src="http://localhost/skandal/public/images/logo.jpg" alt="studioskandal" />
            </div>  
            <div className="header__input">
                <input type="text" placeholder="Search" />
               
                <SearchIcon className="header__input_button" />
               
            </div>
            <div className="header__text">
                <label >THE BEST <span>FREE PORN</span> SITE</label>
            </div>
            {/*
import SettingsIcon from '@material-ui/icons/Settings';
<div className="header__right">
                 <button  >Join For FREE</button>
                 <label>Log in</label>
                 <SettingsIcon className="header_input_options" />
    </div>*/}
            <div>
                
            </div>
        </div>
        
    )
}

export default Header



