import React  from 'react';
import './App.css';
import './component/Header/Header';
import Header from './component/Header/Header';
import Footer from './component/Footer/Footer';
import BodyContent from './component/BodyContent';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {
  return (
    <div className="app">
      
       
           {/*HEADER*/}
       {/*SIDEBAR*/}
       {/*RECOMENDED VIDEO*/}
       <Router>
       <Header/>    
       <div className="app_page">
            <Switch>
                <Route path="/c/asian">
                      <BodyContent />
                </Route>
                <Route path="/c/barat">
                      <BodyContent />
                </Route>
                
                <Route path="/c/hentai">
                     <BodyContent />
                </Route>
               
                <Route path="/c/indonesia">
                    <BodyContent />
                </Route>
                <Route path="/c/viral">
                    <BodyContent />
                </Route>
                <Route path="/c/jepang">
                    <BodyContent />
                </Route>
                
                <Route path="/">
                     <BodyContent />
                </Route>
            </Switch>
            </div> 
        <Footer/>   
       </Router>
        
      
       
    </div>
  );
}

export default App;



